<template>
  <v-layout row
            justify-center>
    <v-dialog v-model="dialog"
              persistent
              max-width="500px">
      <v-btn slot="activator"
             icon
             dark>
        <v-icon color="grey-light-1">add_alert</v-icon>
      </v-btn>
      <v-card>
        <v-card-title class="headline"
                      v-if="event.alert == 0">Create Alert</v-card-title>
        <v-card-title class="headline"
                      v-if="event.alert == 1">Remove Alert</v-card-title>
        <v-card-text v-if="event.alert == 0">Are you sure you want to enable this event for alerting?</v-card-text>
        <v-card-text v-if="event.alert == 1">Are you sure you want to remove this event for alerting?</v-card-text>
        <v-card-text>
          <v-form ref="form"
                  v-model="valid"
                  lazy-validation>
            <v-text-field prepend-icon="mail"
                          v-model="email"
                          :rules="emailRules"
                          label="E-mail"
                          required></v-text-field>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn flat
                     @click.native="dialog = false">Cancel</v-btn>
              <v-btn flat
                     color="primary"
                     @click.native="alert"
                     v-if="event.alert == 0">Add</v-btn>
              <v-btn flat
                     color="primary"
                     @click.native="alert"
                     v-if="event.alert == 1">Remove</v-btn>
            </v-card-actions>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
export default {
  props: ["event"],

  data() {
    return {
      dialog: false,
      valid: true,
      name: "",
      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length <= 10) || "Name must be less than 10 characters"
      ],
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+/.test(v) || "E-mail must be valid"
      ]
    };
  },

  methods: {
    alert() {
      this.dialog = false;
      //console.log("Alert created");
    }
  }
};
</script>
