import { HTTP } from "@/main";

export const GetAvailSrvs = () => {
  return HTTP.get("/eventlogs/GetAvailableServers").then(response => {
    return response.data;
  });
};

export const GetVMEvents = data => {
  return HTTP.post("/eventlogs/GetEvents", {
    server: data.server,
    source: data.source
  }).then(response => {
    return response.data;
  });
};

export const ResetEvent = data => {
  return HTTP.post("/eventlogs/ResetEvent/" + data.id).then(response => {
    return response.data;
  });
};
