<template>
  <v-data-table
    :headers="headers"
    :items="logs"
    item-key="id"
    class="events pl-2 pr-2"
    :search="search"
    :rows-per-page-items="rowsperpage"
  >
    <template slot="items" slot-scope="props">
      <tr @click="props.expanded = !props.expanded">
        <td class="text-xs-left pl-3">{{ props.item.recurrences }}</td>
        <td class="text-xs-left pl-3">{{props.item.timeCreated | timeAgo }}</td>
        <td class="text-xs-left pl-3">{{ props.item.eventId }}</td>
        <td class="text-xs-center">
          <v-flex d-inline-flex align-center justify-space-between>
            <v-btn icon class="mx-0">
              <v-icon v-if="props.item.level=='Warning'" color="orange">warning</v-icon>
              <v-icon v-if="props.item.level=='Critical'" color="red">error</v-icon>
              <v-icon v-else-if="props.item.level=='Error'" color="red">error</v-icon>
            </v-btn>
          </v-flex>
        </td>
        <td class="text-xs-left hide-lg-and-down pl-4">
          {{
          (props.item.providerName).substring(0,16) }}
        </td>
        <td class="text-xs-left hide-xs-and-down hide-sm-and-down hide-md-and-down pl-4">
          {{
          (props.item.providerName) }}
        </td>
        <td class="text-xs-left hide-xs-and-down pl-3">{{ props.item.machineName }}</td>
        <td class="text-xs-left hide-xs-and-down hide-sm-and-down hide-md-and-down pl-3">
          {{
          (props.item.mainMessage).substring(0,90) }}
        </td>
      </tr>
    </template>
    <template slot="expand" slot-scope="props">
      <v-card flat class="tbExpanded">
        <!--  <v-card flat class="hide-md-and-up"> -->
        <v-layout>
          <v-flex xs4>
            <v-card-actions class="d-block verticalDiv">
              <div class="subheading">Server Name:</div>
              <div>{{ props.item.machineName }}</div>
            </v-card-actions>
          </v-flex>
          <v-flex>
            <v-divider vertical></v-divider>
          </v-flex>
          <v-flex sx4>
            <v-card-actions class="d-block verticalDiv">
              <div class="subheading">Started</div>
              <div>{{ props.item.firstCreated | dateFormatted}}</div>
            </v-card-actions>
          </v-flex>
          <v-flex>
            <v-divider vertical></v-divider>
          </v-flex>
          <v-flex sx4>
            <v-card-actions class="verticalDiv">
              <div class="justify-space-around layout d-block">
                <div class="subheading d-block">Reset</div>
                <div class="pl-2">
                  <v-btn icon @click="reset(props.item)">
                    <v-icon color="grey-light-1">delete</v-icon>
                  </v-btn>
                </div>
              </div>
            </v-card-actions>
          </v-flex>
          <!--  <v-flex>
                        <v-divider vertical></v-divider>
          </v-flex>-->
          <!-- <v-flex sx2>
                        <v-card-actions class="verticalDiv">
                            <div class="d-block">
                                <div class="subheading d-block">
                                    Alert Me
                                </div>
                                <div class="">
                                    <AlertMe :event="props.item"></AlertMe>
                                </div>
                            </div>
                        </v-card-actions>
          </v-flex>-->
        </v-layout>
        <v-card-actions class="d-block">
          <div class="subheading">Message:</div>
          <div>
            <pre>
                      {{ props.item.mainMessage }}
                    </pre>
          </div>
        </v-card-actions>
      </v-card>
    </template>
  </v-data-table>
</template>

<script>
import distanceInWordsStrict from "date-fns/distance_in_words_strict";
import format from "date-fns/format";
import AlertMe from "@/components/VirtualMachines/Eventlogs/AlertMe";
import { ResetEvent } from "@/services/api/VirtualMachines";
import { Notify } from "@/services/HelperService";

export default {
  components: {
    AlertMe
  },
  props: ["headers", "logs", "search"],
  data() {
    return {
      pagination: {
        sortBy: "timeCreated",
        descending: true,
        rowsPerPage: 10
      },
      rowsperpage: [
        10,
        15,
        25,
        {
          text: "$vuetify.dataIterator.rowsPerPageAll",
          value: -1
        }
      ]
    };
  },
  filters: {
    timeAgo: function(value) {
      let result = distanceInWordsStrict(new Date(), new Date(value), {
        addSuffix: true
      });
      if (result.includes("hours")) return result.replace("hours", "hrs");
      if (result.includes("hour")) return result.replace("hour", "hr");
      if (result.includes("minutes")) return result.replace("minutes", "mins");
      if (result.includes("minute")) return result.replace("minute", "min");
      if (result.includes("seconds")) return result.replace("seconds", "sec");
      return result;
    },
    dateFormatted: function(value) {
      return format(new Date(value), "MM/DD/YYYY HH:mm");
    }
  },
  methods: {
    reset(event) {
      ResetEvent(event)
        .then(data => {
          const index = this.logs.findIndex(el => {
            return el.id === data.id;
          });
          this.logs.splice(index, 1);
        })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.loading = false;
        });
    },
    alertMe() {}
  }
};
</script>

<style lang="scss" scoped>
.tbExpanded {
  background-color: darken($color: #fff, $amount: 5);
  border-radius: 5px;
}
pre {
  white-space: pre-line; /* CSS 3 */
  white-space: -moz-pre-line; /* Mozilla, since 1999 */
  white-space: -pre-line; /* Opera 4-6 */
  white-space: -o-pre-line; /* Opera 7 */
  white-space: pre-line;
  white-space: -moz-pre-line;
  white-space: -pre-line;
  white-space: -o-pre-line;
  /* These are technically the same, but use both */
  overflow-wrap: break-word;
  word-wrap: break-word;

  -ms-word-break: break-all;
  /* This is the dangerous one in WebKit, as it breaks things wherever */
  word-break: break-all;
  /* Instead use this non-standard one: */
  word-break: break-word;

  /* Adds a hyphen where the word breaks, if supported (No Blink) */
  -ms-hyphens: auto;
  -moz-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;

  font-family: inherit;
}
</style>
