<template>
  <section class="checkFeeds pt-2 mx-3">
    <page-intro :introName="introName"></page-intro>
    <v-card>
      <v-card-title>
        <v-layout row
                  wrap>
          <v-flex xs12
                  sm4
                  md3
                  class="pr-3">
            <v-autocomplete v-model="select"
                            dense
                            :items="serverList"
                            :error-messages="selectErrors"
                            label="Select VM..."
                            required
                            @change="$v.select.$touch()"
                            @blur="$v.select.$touch()">
            </v-autocomplete>
          </v-flex>
          <v-flex xs12
                  sm4
                  md2>
            <v-select v-model="source"
                      :items="sources"
                      label="Source"
                      required>
            </v-select>
          </v-flex>
          <v-flex xs12
                  sm4
                  md4>
            <div class="pl-2">
              <v-btn :disabled="this.$v.$invalid"
                     v-if="!loading"
                     color="tfgprimary"
                     class="mt-3 ml-0"
                     round
                     small
                     :dark="!this.$v.$invalid"
                     @click="GetEvents()">
                Find
              </v-btn>
              <v-progress-circular indeterminate
                                   color="primary"
                                   class="mt-3 ml-0"
                                   v-if="loading">
              </v-progress-circular>
              <!-- <exportMenu v-if="srvEvents.length > 0"
                          :data="srvEvents"
                          :headers="headers"></exportMenu> -->

            </div>
          </v-flex>
          <v-flex xs6
                  md3>
            <v-text-field v-model="search"
                          prepend-icon="filter_list"
                          label="Filter results"
                          single-line
                          hide-details>
            </v-text-field>
          </v-flex>
        </v-layout>
      </v-card-title>
      <eventTable :headers="headers"
                  :search="search"
                  :logs="srvEvents"></eventTable>
    </v-card>
  </section>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";
import { GetAvailSrvs, GetVMEvents } from "@/services/api/VirtualMachines";
import { Notify } from "@/services/HelperService";
import exportMenu from "@/components/exportMenu";
import eventTable from "@/components/VirtualMachines/Eventlogs/EventTable";
import PageIntro from "@/components/PageIntro.vue";

export default {
  mixins: [validationMixin],

  validations: {
    select: {
      required
    }
  },

  components: {
    exportMenu,
    eventTable,
    PageIntro
  },

  data() {
    return {
      loading: false,
      introName: "Windows Eventlog Viewer",
      search: "",
      headers: [
        { text: "Count", value: "recurrences", align: "left" },
        { text: "LastOcc", value: "timeCreated", align: "left" },
        {
          text: "ID",
          value: "eventId",
          align: "left",
          class: ""
        },
        { text: "Severity", value: "level", align: "center" },
        {
          text: "Source",
          value: "providerName",
          align: "left"
        },
        {
          text: "Server Name",
          value: "machineName",
          align: "left",
          class: "hide-xs-and-down"
        },
        {
          text: "Message",
          value: "mainMessage",
          align: "left",
          class: "hide-xs-and-down hide-sm-and-down hide-md-and-down"
        }
      ],
      select: "",
      source: "Application",
      sources: ["Application", "System"],
      serverList: [],
      srvEvents: []
    };
  },

  computed: {
    selectErrors() {
      const errors = [];
      if (!this.$v.select.$dirty) return errors;
      !this.$v.select.required && errors.push("Server is required");
      return errors;
    }
  },

  methods: {
    GetEvents() {
      this.loading = true;
      GetVMEvents({ server: this.select, source: this.source })
        .then(data => {
          this.srvEvents = data;
        })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.loading = false;
        });
    }
  },

  mounted() {
    this.loading = true;
    GetAvailSrvs()
      .then(data => {
        this.serverList = data.sort();
      })
      .catch(error => {
        this.$notify(Notify(error));
      })
      .finally(() => {
        this.loading = false;
      });
  }
};
</script>
